@font-face {
  font-family: "LatoRegular";
  src: url("./fonts/Lato-Regular.woff2");
}

.navBar {
  background-color: white;
  min-width: 14%;
  max-width: 14%;
  padding-left: 4%;
  padding-right: 4%;
}

.title {
  font-family: 'LatoRegular';
  font-weight: 500;
  font-size: 36px;
  margin-top: 20%;
  margin-bottom: 13%;
  line-height: 38px;
}

.description {
  font-family: 'LatoRegular';
  font-weight: 200;
  font-size: 14px;
  color: #AAA;
  margin-top: 13%;
  margin-bottom: 17%;
  line-height: 20px;

}

hr {
  width: 90px;
}

.content {
  background-color: #F0F0F0;
  width: 100%;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: row;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
